            p La contabilidad refleja la realidad econmica y financiera de la empresa, en la que se tiene en cuenta el ciclo contable para obtener como producto los estados financieros de propsito general. En cumplimiento de los estndares internacionales se aplican los criterios de reconocimiento, medicin, presentacin y revelacin de los estados financieros, estipulados en las polticas contables diseadas por la entidad, de acuerdo con la Norma Internacional de Informacin Financiera (NIIF) y los grupos de presentacin de informacin financiera.

    .row.justify-content-center.mb-4
      .col-lg-8
        p.mb-4 Cabe resaltar que los estados financieros que se generan requieren de la aplicacin de tcnicas de anlisis e indicadores financieros; cuyo objetivo es interpretar los valores monetarios para la elaboracin de informes comprensibles y tiles para la toma de decisiones de liquidez, solvencia y rentabilidad.
        .cajon.color-primario.p-4.mb-2
          p.mb-0 El anlisis financiero es una herramienta de apoyo a la gerencia porque establece los resultados financieros para identificar las debilidades y fortalezas, con el fin de disear e implementar estrategias para dar valor agregado a la empresa. Adicionalmente, es til para las entidades financieras y acreedores, teniendo en cuenta que determinan la capacidad de endeudamiento y liquidez de la compaa. Finalmente, permite a los socios y propietarios conocer los niveles de rentabilidad de la inversin realizada.
      .col-lg-4
