          p.mb-0 Para desarrollar dichas tcnicas es importante conocer la informacin financiera, como mnimo de dos perodos, con el fin de revisar los aumentos, disminuciones y desempeo de la empresa. Los resultados generados, contribuyen en el diseo e implementacin de estrategias que fortalecen la productividad y rentabilidad, adems, de alcanzar los objetivos organizacionales en trminos de eficiencia y eficacia. Las herramientas que se utilizan para llevar a cabo el anlisis financiero son: tcnicas de anlisis horizontal, anlisis vertical y aplicacin de indicadores financieros.
    
    
