    h3.mb-4 Anlisis horizontal
    p.mb-4 Para desarrollar esta tcnica se requiere mnimo dos estados financieros homogneos de perodos consecutivos, cuyo objetivo es realizar el comparativo y determinar los aumentos o disminuciones generados de un perodo al otro, con el fin de tomar decisiones oportunamente. Cabe resaltar que esta tcnica se puede aplicar a dos o ms perodos, con el fin de analizar la trazabilidad de la empresa y evaluar el desempeo de esta.
    p.mb-4 El procedimiento que se desarrolla para aplicar dicha tcnica consiste en determinar la variacin absoluta y variacin relativa, con el fin de analizar los aumentos o disminuciones generados en los perodos analizados:
    .row.justify-content-center.mb-4
      .col-lg-4
        figure.mb-2
          img(src='@/assets/curso/tema1/img24.svg', alt='imagen ambiental').mx-auto
      .col-lg-7
        LineaTiempoD.color-acento-contenido.mb-5
          .row(numero="1" titulo="Variacin absoluta")
            .col-lg-12
              p.mb-4 Para hallar la variacin absoluta se toman los datos de dos perodos, restando el ao actual y el ao anterior, el cual se representa en valores monetarios. La frmula aplicable es la siguiente:
            .col-lg-12
              figure
                img(src='@/assets/curso/tema1/img25.svg', alt='Variacin absoluta').mx-auto

          .row(numero="2" titulo="Variacin relativa")
            .col-lg-12
              p.mb-4 En el caso de la variacin relativa se tiene en cuenta la variacin absoluta (ao actual  ao anterior), dividido en el ao anterior, cabe resaltar que se multiplica por 100 para obtener el porcentaje.
            .col-lg-12
              figure
                img(src='@/assets/curso/tema1/img26.svg', alt='Variacin relativa').mx-auto
    .cajon.color-acento-contenido.p-4.mb-5
